import create from "zustand";
import { useCallback } from "react";
import { Intro, Loading, Main, Finished } from "../components/UX/Sections";
import { areaData } from "./areaData";
import { Vector3 } from "three";

/////////////////////////////////////////////////////////////////////////////////////
// Default Variables
/////////////////////////////////////////////////////////////////////////////////////
const showLeva = 1;
/// Sections
const sectionObj = {
  Intro: <Intro />,
  Loading: <Loading />,
  Main: <Main />,
  Finished: <Finished />
};
const sectionKeys = Object.keys(sectionObj);
const sectionKey = sectionKeys[0];
const section = sectionObj[sectionKey];
//// Areas
const areas = areaData;
const areaIndex = 0;
const areaKeys = Object.keys(areas);
const areaCurrent = areas[areaKeys[areaIndex]];
/// Examples
/////////////////////////////////////////////////////////////////////////////////////
// Store
/////////////////////////////////////////////////////////////////////////////////////
export const useApp = create((set, get) => ({
  /////////////////////////////////////////////////////////////////////////////////////
  // Init
  /////////////////////////////////////////////////////////////////////////////////////
  init: () => {
    console.log("init");
  },
  showLeva,
  /////////////////////////////////////////////////////////////////////////////////////
  // HUD Navigation & Phases
  /////////////////////////////////////////////////////////////////////////////////////
  section,
  sectionObj,
  sectionKey,
  sectionKeys,
  goToSection: (sectionKey) => {
    // if passed variable isn't in phases show available phases
    if (!sectionObj[sectionKey]) {
      console.log(`${sectionKey} is not in sections`);
      console.log(get().sectionKeys);
      return;
    }
    const section = get().sectionObj[sectionKey];
    set((state) => ({ section, sectionKey }));
  },
  BtnGoToSection: (goToOverlay) => {
    return useCallback(() => {
      get().goToSection(goToOverlay);
    }, [goToOverlay]);
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // 3D model Libararies
  /////////////////////////////////////////////////////////////////////////////////////
  areas,
  areaKeys,
  areaIndex,
  areaCurrent,
  // Locking orbit controlls during animation
  orbitEnabled: true,
  setOrbitEnabled: (orbitEnabled) => set((state) => ({ orbitEnabled })),
  // React Spring API
  springAPI: null,
  setSpringAPI: (springAPI) => set((state) => ({ springAPI })),
  // Functions
  moveFovTo: (newFovTo) => {
    const API = get().springAPI;
    const camerafov = get().camera.fov;
    API.set({
      springFOV: camerafov
    });
    API.start({
      springFOV: newFovTo
    });
  },
  moveCameraTo: (newCameraTo) => {
    const API = get().springAPI;
    const cameraPos = get().camera.position;
    API.set({
      springCameraPos: [cameraPos.x, cameraPos.y, cameraPos.z]
    });
    API.start({
      springCameraPos: newCameraTo
    });
  },
  moveTargetTo: (newTargetTo) => {
    const API = get().springAPI;
    const target = get().orbitControl.current.target;
    API.set({
      springTargetPos: [target.x, target.y, target.z]
    });
    API.start({
      springTargetPos: newTargetTo
    });
  },
  // Buttons for Areas via key
  BtnGoToArea: (key) => {
    return useCallback(() => {
      const a = get().areas[key].view;
      get().moveCameraTo(a.camera);
      get().moveFovTo(a.fov);
      get().moveTargetTo(a.target);
    }, [key]);
  },
  // Utility
  saveCameraPos: () => {
    const copyTextToClipboard = (text) => {
      var textArea = document.createElement("textarea");
      textArea.style.position = "fixed";
      textArea.style.top = 0;
      textArea.style.left = 0;
      textArea.style.opacity = 0;
      textArea.style.pointerEvents = "none";
      textArea.value = text;

      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();

      try {
        var successful = document.execCommand("copy");
        var msg = successful ? "successful" : "unsuccessful";
        console.log("Copying text command was " + msg);
        if (successful) console.log(text);
      } catch (err) {
        console.log("Oops, unable to copy");
      }

      document.body.removeChild(textArea);
    };

    const camera = get().camera;
    const C = {
      x: parseFloat(camera.position.x.toFixed(3)),
      y: parseFloat(camera.position.y.toFixed(3)),
      z: parseFloat(camera.position.z.toFixed(3)),
      f: camera.fov
    };

    var dist = 0.2;
    var cwd = new Vector3();
    camera.getWorldDirection(cwd);
    cwd.multiplyScalar(dist);
    cwd.add(camera.position);

    const T = {
      x: parseFloat(cwd.x.toFixed(3)),
      y: parseFloat(cwd.y.toFixed(3)),
      z: parseFloat(cwd.z.toFixed(3))
    };
    const copyText = `
      target:[${T.x},${T.y},${T.z}],
      camera:[${C.x},${C.y},${C.z}],
      fov:${C.f}
    `;
    copyTextToClipboard(copyText);
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // Three Objects
  /////////////////////////////////////////////////////////////////////////////////////
  gl: null,
  scene: null,
  camera: null,
  orbitControl: null,
  setThree: ({ gl, scene, camera, orbitControl }) => {
    set((state) => ({ gl, scene, camera, orbitControl }));
  },
  /////////////////////////////////////////////////////////////////////////////////////
  // Progress Vars
  /////////////////////////////////////////////////////////////////////////////////////
  active: null,
  progress: null,
  errors: null,
  item: null,
  loaded: null,
  total: null,
  setProgress: ({ active, progress, errors, item, loaded, total }) => {
    set((state) => ({ active, progress, errors, item, loaded, total }));
  }
}));
