import React, { useRef, memo, useEffect } from "react";
import {
  OrbitControls,
  PerspectiveCamera,
  useProgress,
  Sphere
} from "@react-three/drei";
import { useThree, useFrame } from "@react-three/fiber";
import { useApp } from "../../utility/useApp";
import { useSpring, a } from "@react-spring/three";
import { useControls } from "leva";

function Controls() {
  const myCamera = useRef();
  const orbitControl = useRef();

  const setThree = useApp((state) => state.setThree);
  setThree({ ...useThree(), orbitControl });

  const setProgress = useApp((state) => state.setProgress);
  setProgress({ ...useProgress() });

  const { view } = useApp((state) => state.areaCurrent);
  const orbitEnabled = useApp((state) => state.orbitEnabled);
  const setOrbitEnabled = useApp((state) => state.setOrbitEnabled);
  const cameraMotion = { mass: 1, tension: 280, friction: 60 };

  const [
    { springCameraPos, springTargetPos, springFOV },
    springAPI
  ] = useSpring(() => ({
    springTargetPos: view.target,
    springCameraPos: view.camera,
    springFOV: view.fov,
    config: cameraMotion,
    onStart: () => setOrbitEnabled(false),
    onRest: () => setOrbitEnabled(true)
  }));

  const setSpringAPI = useApp((state) => state.setSpringAPI);
  useEffect(() => setSpringAPI(springAPI), [springAPI, setSpringAPI]);

  const AOrbit = a(OrbitControls);
  const APerspectiveCamera = a(PerspectiveCamera);
  const ASphere = a(Sphere);

  const { TargetHelper } = useControls({ TargetHelper: false });

  useFrame(() => {
    orbitControl.current.update();
    myCamera.current.updateProjectionMatrix();
  });

  return (
    <>
      <APerspectiveCamera
        ref={myCamera}
        position={springCameraPos}
        fov={springFOV}
        makeDefault
      />
      <AOrbit
        ref={orbitControl}
        camera={myCamera.current}
        enabled={orbitEnabled}
        target={springTargetPos}
      />
      {TargetHelper && (
        <ASphere position={springTargetPos} args={[0.01, 10, 10]}>
          <meshBasicMaterial
            attach="material"
            color="hotpink"
            transparent
            opacity={0.5}
          />
        </ASphere>
      )}
    </>
  );
}
export default memo(Controls);

