import React from "react";
import { useApp } from "../../utility/useApp";
import { AnimatePresence } from "framer-motion";
import { Stats } from "@react-three/drei";

export default function HUD() {
  const sectionKey = useApp((state) => state.sectionKey);
  const section = useApp((state) => state.section);

  return (
    <main className={`HUD ${sectionKey}`}>
      <AnimatePresence exitBeforeEnter>{section}</AnimatePresence>
      <Stats />
    </main>
  );
}
